}

.container .card:hover .content .contentBx h3 {
  color: black;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 1.2rem;
  text-align: center;
  margin: 20px 0px 10px;
  line-height: 1.1em;
}

.container .card:hover .content .contentBx h3 span {
  margin: 20px;
  font-size: 1rem;
  font-weight: 500;
  text-transform: none;
  text-transform: initial;
}

.container .card .sci {
  margin-top: 20px;
