/* Menu-Sidebar */

.sidebar {
  position: absolute;
  width: 23%;
  background: rgb(28, 28, 28);
  font-size: 0.65em;
}
.sidebar-heading {
  color: white;
  text-align: center;
  font-size: 1.7rem;
  padding: 10px 10px 35px 10px;
}
.navv {
  margin: 0 15%;
  text-align: center;
  font-weight: bold;
}
.navv ul {
  padding: 0px;
  list-style: none;
}
.navv ul li {
  position: relative;
  margin: 2.5em 0;
}
.navv ul li a {
  line-height: 5em;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.4em;
  color: rgb(185, 185, 185);
  display: block;
  transition: all ease-out 300ms;
}
.navv ul li.active a {
  color: white;
}
.navv ul li:not(.active)::after {
  opacity: 0.7;
}
.navv ul li:not(.active):hover a {
  color: rgba(255, 255, 255);
}
.navv ul li::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.3em;
  background: black;
  left: 0;
  bottom: 0;
  background-image: linear-gradient(to right, #fb7ba2, #fce043);
}

