  display: flex;
  font-family: 'Roboto', sans-serif;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #737373;
  background: var(--bg);
  color: #fafafa;
  color: var(--color);
  -webkit-perspective: 1200px;
          perspective: 1200px;
}

.Error404 {
  -webkit-animation: swing 2s infinite alternate ease-in-out;
          animation: swing 2s infinite alternate ease-in-out;
  -webkit-animation: swing var(--speed) infinite alternate ease-in-out;
          animation: swing var(--speed) infinite alternate ease-in-out;
  font-size: clamp(5rem, 40vmin, 20rem);
  font-family: 'Open Sans', sans-serif;
  margin: 0;
  margin-bottom: 1rem;
  letter-spacing: 1rem;
  -webkit-transform: translate3d(0, 0, 0vmin);
          transform: translate3d(0, 0, 0vmin);
  --x: calc(50% + (var(--swing-x) * 0.5) * 1%);
  background: radial-gradient(#e6e6e6, #7a7a7a 45%) var(--x) 100%/200% 200%;
  background: radial-gradient(var(--lit-header), var(--header) 45%) var(--x) 100%/200% 200%;
  -webkit-background-clip: text;
  color: transparent;
}

.Error404:after {
  -webkit-animation: swing 2s infinite alternate ease-in-out;
          animation: swing 2s infinite alternate ease-in-out;
  -webkit-animation: swing var(--speed) infinite alternate ease-in-out;
          animation: swing var(--speed) infinite alternate ease-in-out;
  content: "404";
  position: absolute;
  top: 0;
  left: 0;
  color: #000;
  color: var(--shadow);
  -webkit-filter: blur(1.5vmin);
          filter: blur(1.5vmin);
  -webkit-transform: scale(1.05) translate3d(0, 12%, -10vmin) translate(calc((0 * 0.05) * 1%), calc((var(--swing-y) * 0.05) * 1%));
          transform: scale(1.05) translate3d(0, 12%, -10vmin) translate(calc((0 * 0.05) * 1%), calc((var(--swing-y) * 0.05) * 1%));
  -webkit-transform: scale(1.05) translate3d(0, 12%, -10vmin) translate(calc((var(--swing-x, 0) * 0.05) * 1%), calc((var(--swing-y) * 0.05) * 1%));
          transform: scale(1.05) translate3d(0, 12%, -10vmin) translate(calc((var(--swing-x, 0) * 0.05) * 1%), calc((var(--swing-y) * 0.05) * 1%));
}

.cloak {
  animation: swing 2s infinite alternate-reverse ease-in-out;
  animation: swing var(--speed) infinite alternate-reverse ease-in-out;
  height: 100%;
  width: 100%;
  -webkit-transform-origin: 50% 30%;
          transform-origin: 50% 30%;
  -webkit-transform: rotate(calc(var(--swing-x) * -0.25deg));
          transform: rotate(calc(var(--swing-x) * -0.25deg));
  background: radial-gradient(40% 40% at 50% 42%, transparent, #000 35%);
}

.cloak__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
}

.cloak__container {
  height: 250vmax;
  width: 250vmax;
  position: absolute;
